@import url(https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&display=swap);
.header-container {
  background-color: #10162f;
  padding: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.header-container__logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.header-container__logo h1 {
  margin-top: 40px;
}

.header-container__logo img {
  width: 120px;
  height: 100px;
}

.header-container__score-info {
  color: white;
  margin-right: 2rem;
  padding: 0;
}

.header-container__score-info li {
  list-style: none;
}

.la-star {
  color: yellow;
}

@media (max-width: 768px) {
  .header-container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .header-container__logo {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .header-container__logo h1 {
    margin-top: 0;
    margin-bottom: 0;
  }

  .header-container__score-info {
    display: -webkit-flex;
    display: flex;
    margin-right: 0;
  }

  .header-container__score-info li {
    -webkit-align-self: center;
            align-self: center;
    padding-right: 1rem;
  }
}

* {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Courier Prime', monospace, Arial 
}

